// Configurao de verso da aplicao
// Este arquivo  atualizado automaticamente durante o build
export const APP_VERSION = '0.2.1';
export const BUILD_TIMESTAMP = 1753759267338;

// Funo para verificar se h uma nova verso disponvel
export const checkForUpdates = () => {
  const currentVersion = localStorage.getItem('app_version');
  
  if (currentVersion !== APP_VERSION) {
    localStorage.setItem('app_version', APP_VERSION);
    return true; // Nova verso detectada
  }
  
  return false; // Mesma verso
};

// Funo para forar atualizao
export const forceUpdate = () => {
  // Limpa cache do service worker
  if ('serviceWorker' in navigator) {
    navigator.serviceWorker.getRegistrations().then(function(registrations) {
      for(let registration of registrations) {
        registration.unregister();
      }
    });
  }
  
  // Fora recarregamento
  window.location.reload(true);
};
